@import '~modern-normalize/modern-normalize';
@import './styles.scss';

body {
  margin: 0;
  min-height: 100vh;
  font-family: 'Roboto', sans-serif;
  font-style: normal;
  color: $white-color;
  background-color: $black-color;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}

ul,
ol {
  list-style: none;
  padding: 0;
  margin: 0;
}

button {
  padding: 0;
  border: none;
  font: inherit;
  color: inherit;
  background-color: transparent;
  cursor: pointer;
}

a {
  text-decoration: none;
  color: inherit;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

// remove  browser styles for inputs when autocomplete is enabled
input:-webkit-autofill {
  -webkit-box-shadow: 0 0 0 50px $black-color inset;
  -webkit-text-fill-color: $white-color;
}

.body-scroll-lock {
  height: 100vh;
  overflow-y: hidden;
}
